// This file is used as template for the __edge-functions__.js file.
// This template is also used by fiddle to run the edge functions in the browser.

// Connascence of convention with the quickjs-runtime invocation protocol.
// quickjs-runtime always invokes a single exported function passing its
// runtime object (used to interact with the Wasm host) and the request ID as arguments.
module.exports = function (runtime, edgioRequestId) {
  // All the edge functions are bundled into a single map for fast lookup.
  // The map is inserted on this place during the build process.
  const edgeFunctions = '__INSERT_EDGE_FUNCTIONS_MAP_HERE__'

  // Load the SDK and pass it the runtime, request ID and edge functions map.
  // During the build of @edgio/core, the sdk is bundled into the template.
  // This bundled template can be later used to run the edge functions in the browser.
  const sdk = require('../../../src/runtime/edge-functions/sdk.js')
  sdk(runtime, edgioRequestId, edgeFunctions)
}
