/*global globalThis*/

// Edgio SDK for QuickJS runtime. This file is evaluated in the runtime before
// any of the edge function developer's code runs.
// Top wrapper function so that we can keep internals private within the scope of the function.
// There are two ways to export anything from the function:
// * Assign it to globalThis so that end developer code can access it
// * Return it from the function so that the runtime Rust code can access it
// The injected object is a proxy object created in the Rust code that forwards
// all the calls to the host API.
module.exports = function ({ host, utility }, edgioRequestId, edgeFunctions) {
  // wrap up built in JSON to get metrics on JSON.parse and JSON.stringify
  // if you don't have built-in compress/decompress
  // would allow a certain amount of customer metrics
  const STDOUT_FD = 1
  const STDERR_FD = 2

  // Dictionary namespaces used here, in sailfish and EdgeFunction.ts
  const ENV_VAR_NAMESPACE_EDGIO_CONFIG = 'edgio-config'
  const ENV_VAR_NAMESPACE_EDGIO_ORIGINS = 'edgio-origins'
  const ENV_VAR_NAMESPACE_EDGIO_USRVAR = 'edgio-usrvar'

  // We use symbols for all internal properties to improve the performance and avoid
  // accidental access or conflicts from user code, including JSON serialization.
  const EdgioId = Symbol('edgio-id')
  const Done = Symbol('done')
  const Options = Symbol('options')
  const Headers = Symbol('headers')
  const BodyUsed = Symbol('body-used')
  const BodyDefined = Symbol('body-defined')
  const GetBody = Symbol('get-body')
  const Namespace = Symbol('namespace')
  const AppendFn = Symbol('append-fn')
  const CheckFn = Symbol('check-fn')
  const GetFn = Symbol('get-fn')
  const KeysFn = Symbol('keys-fn')
  const Redirected = Symbol('redirected')
  const RemoveFn = Symbol('remove-fn')
  const SetFn = Symbol('set-fn')
  const Url = Symbol('url')
  const ErrorCode = Symbol('error-code')

  const InternalErrorPrefix = 'Internal Error: '

  // object-inspect https://github.com/inspect-js/object-inspect has to be bundled
  // in a special manner to prevent it from using Node's built-in util.inspect.
  // This is done during SDK bundling (see ./bundle-object-inspect.sh)
  const inspect = require('./bundled-object-inspect.js')

  // Writes the arguments to the given file descriptor by leveraging inspect facility.
  let consoleGroupIndentDepth = 0
  const CONSOLE_GROUP_INDENT_STEP = 2
  const getConsoleIndent = () => ' '.repeat(consoleGroupIndentDepth)
  function wrappedConsoleWrite(fdWrite, ...args) {
    // Always indent the output.
    fdWrite(getConsoleIndent())

    if (args.length > 0) {
      // If the first argument is a string, we may need to do some formatting.
      let firstArg = args[0]
      if (typeof firstArg === 'string') {
        // We now have the fist arg. We will try to format it and then put it back
        // on the args array.
        args.shift()
        // Parse firstArg as a format string and replace the placeholders with
        // the remaining arguments.
        firstArg = firstArg.replace(/%[oOdisf]/g, match => {
          if (args.length === 0) {
            return match
          }

          const arg = args.shift()
          switch (match) {
            case '%o':
            case '%O':
              return inspect(arg)
            case '%d':
            case '%i':
              return parseInt(arg, 10).toString()
            case '%s':
              return arg.toString()
            case '%f':
              return parseFloat(arg).toString()
            default:
              return match
          }
        })
        args.unshift(firstArg)
      }
    }

    // Output the arguments separated by spaces. We don't use .join(' ') to
    // avoid the extra string allocations. The tradeoff is more hostcalls.
    for (let i = 0; i < args.length; i++) {
      if (i !== 0) {
        fdWrite(' ')
      }

      const arg = args[i]
      const type = typeof arg
      // Depending on the type, we either convert the argument to a string
      // or we use inspect to convert it to a string.
      switch (type) {
        case 'string':
          fdWrite(arg)
          break
        case 'number':
        case 'function':
          fdWrite(arg.toString())
          break
        case 'boolean':
          fdWrite(arg ? 'true' : 'false')
          break
        case 'undefined':
        case 'null':
          fdWrite(type)
          break
        default:
          if (arg === null) {
            fdWrite('null')
          } else {
            fdWrite(inspect(arg))
          }
          break
      }
    }

    fdWrite('\n')
  }
  // We wrap consoleWrite in a try/catch block to avoid crashing the runtime on
  // logging and allow the user to see the error.
  function consoleWrite(fd, ...args) {
    const fdWrite = utility.stream_write_all.bind(this, fd)
    try {
      return wrappedConsoleWrite(fdWrite, ...args)
    } catch (e) {
      fdWrite('Exception during console output: ')
      fdWrite(e.toString())
      fdWrite('\n')
    }
  }

  // An implementation of console object.
  const consoleCounters = new Map()
  globalThis.console = {
    assert: function (condition, ...args) {
      if (!condition) {
        consoleWrite(STDERR_FD, 'Assertion failed:', ...args)
      }
    },
    clear: function () {
      // In a non-interactive environment, `clear` is a no-op.
    },
    count: function (label) {
      consoleCounters.set(label, (consoleCounters.get(label) || 0) + 1)
      globalThis.console.log(`${label}: ${consoleCounters.get(label)}`)
    },
    countReset: function (label) {
      consoleCounters.set(label, 0)
    },
    debug: consoleWrite.bind(globalThis.console, STDOUT_FD),
    dir: function (...args) {
      // In a non-interactive environment, `dir` is the same as `log`.
      return globalThis.console.log(...args)
    },
    // In a non-interactive environment, `dirxml` is the same as `log`.
    dirxml: function (...args) {
      // In a non-interactive environment, `dir` is the same as `log`.
      return globalThis.console.log(...args)
    },
    error: consoleWrite.bind(globalThis.console, STDERR_FD),
    group: function (...args) {
      if (args !== undefined) {
        globalThis.console.log(...args)
      }

      consoleGroupIndentDepth += CONSOLE_GROUP_INDENT_STEP
    },
    groupCollapsed: function (...args) {
      // In a non-interactive environment, `groupCollapsed` is the same as `group`.
      return globalThis.console.group(...args)
    },
    groupEnd: function () {
      consoleGroupIndentDepth = Math.max(consoleGroupIndentDepth - CONSOLE_GROUP_INDENT_STEP, 0)
    },
    info: consoleWrite.bind(globalThis.console, STDOUT_FD),
    log: consoleWrite.bind(globalThis.console, STDOUT_FD),
    profile: function () {
      // In a non-interactive environment, `profile` is a no-op.
    },
    table: function (data, columns) {
      // Not implemented for now - just print the object.
      return globalThis.console.warn('table not implemented', data, columns)
    },
    time: function () {
      // Not implemented.
    },
    timeEnd: function (label) {
      // Not implemented.
      return globalThis.console.warn('timeEnd not implemented', label)
    },
    timeLog: function (label) {
      // Not implemented.
      return globalThis.console.warn('timeLog not implemented', label)
    },
    timeStamp: function () {
      // In a non-interactive environment, `timeStamp` is a no-op.
    },
    trace: consoleWrite.bind(globalThis.console, STDOUT_FD),
    warn: consoleWrite.bind(globalThis.console, STDERR_FD),
  }

  // sdk.js is provides compatibility between Javascript and the host API
  // The classes in sdk.js are proxies for the host, meaning that all the
  // object's data should be forwarded to or retrieved from the Host API
  // and not stored here in the JavaScript objects. respond_id and request_id
  // are the unique identifiers into the Host API.

  // The design is to put as much code in sdk.js as possible while keeping
  // the code in runtime.rs as minimal as possible. runtime.rs should be a
  // simple API interface for sdk.js. This keeps the javascript to Rust
  // interface easy to maintain.

  // Abstract class to read bodies from Requests and Responses
  class EdgioReadableStreamReader {
    static BUFFER_SIZE = 1024

    // Creates a new ReadableStream object from an Edgio host id.
    constructor(id, getBodyFn) {
      if (this.constructor == EdgioReadableStreamReader) {
        throw new Error(
          `${InternalErrorPrefix}EdgioReadableStreamReader Abstract class cannot be instantiated directly.`
        )
      }
      this[EdgioId] = id
      this[GetBody] = getBodyFn
      this[Done] = false
    }

    async read() {
      // Current hostcall API doesn't allow buffered reading of the bodies
      // so we have to read it all at once.
      const body = new Uint8Array(this[GetBody](this[EdgioId]))
      this[Done] = true

      return { value: body }
    }
  }

  class EdgioReadableResponseStreamReader extends EdgioReadableStreamReader {
    constructor(responseId) {
      super(responseId, host.hostcall_resp_get_body)
    }
  }

  class EdgioReadableRequestStreamReader extends EdgioReadableStreamReader {
    constructor(requestId) {
      super(requestId, host.hostcall_req_get_body)
    }
  }

  // TODO: Replace ReadableStream with a polyfill https://jira.atlas.llnw.com/browse/EAPPS-179
  class EdgioReadableResponseStream {
    constructor(responseId) {
      this[EdgioId] = responseId
    }

    getReader() {
      return new EdgioReadableResponseStreamReader(this[EdgioId])
    }
  }
  class EdgioReadableRequestStream {
    constructor(requestId) {
      this[EdgioId] = requestId
    }
    getReader() {
      return new EdgioReadableRequestStreamReader(this[EdgioId])
    }
  }

  // Implementation of https://developer.mozilla.org/en-US/docs/Web/API/Headers
  // Headers is an abstract proxy class to the edgio host API. It should not
  // store any header information itself.
  class EdgioHeaders {
    // Creates a new EdgioHeaders object from an Edgio host requestId or responseId
    constructor(id, headers, { appendFn, checkFn, getFn, keysFn, removeFn, setFn }) {
      if (this.constructor == EdgioHeaders) {
        throw new Error(
          `${InternalErrorPrefix}Headers Abstract class cannot be instantiated directly.`
        )
      }

      this[EdgioId] = id
      this[AppendFn] = appendFn
      this[CheckFn] = checkFn
      this[GetFn] = getFn
      this[KeysFn] = keysFn
      this[RemoveFn] = removeFn
      this[SetFn] = setFn

      // The spec says headers can be
      // * dictionary: {name1 as string: value1 as string, name2 as string: [value2 as string array] }
      // * TODO: array of pairs: [ [name as string, value as string], [name2 as string, value2 as string] ]
      // * TODO: Headers object
      // https://jira.atlas.llnw.com/browse/EAPPS-180

      // Use our append() method to add these headers, so if there is duplicates
      // they get appended, not overwritten. e.g. { a: 'a', A: 'b' } or [[ a,a ],[a,b]]
      if (Array.isArray(headers)) {
        headers.forEach(h => {
          // ensure each header is an array a pair
          if (!Array.isArray(h)) {
            throw new Error(
              `When passing an array of headers, each array element must be an array of length = 2. '${h}' is type '${typeof h}' not type array.`
            )
          }
          if (h.length !== 2) {
            throw new Error(
              `When passing an array of headers, each array element must be an array of length = 2. '${h}' is array of length ${h.length}.`
            )
          }
          host.hostcall_debug(`.append(${h})`)
          this.append(h[0], h[1])
        })
      } else if (
        headers &&
        Function.prototype.toString.call(headers.constructor).match(/class.*/)
      ) {
        // Check if this item is an instance of a class
        throw new Error(
          `'headers' must be a dictionary or an array of array pairs. Found ${Function.prototype.toString.call(
            headers.constructor
          )}`
        )
      } else if (typeof headers === 'object') {
        Object.keys(headers).forEach(name => {
          this.append(name, headers[name])
        })
      } else if (typeof headers !== 'undefined') {
        // headers === undefined is ok. So this must be the last else if check.
        throw new Error(
          `'headers' must be a dictionary or an array of array pairs. Found type ${typeof headers}.`
        )
      }
    }

    // This is actually more restrictive than the Specification. Chrome allows name types like: null, number.
    _validateName(name) {
      if (name === undefined) {
        throw new Error("The 'name' argument is required, but was not present.")
      }

      if (typeof name !== 'string') {
        throw new Error(`The 'name' argument must be a 'string' type, not a '${typeof name}' type.`)
      }
      return name
    }

    // This is actually more restrictive than the Specification. Chrome allows value types like: null, number.
    _validateValue(value) {
      if (value === undefined) {
        throw new Error("The 'value' argument is required, but was not present.")
      }
      if (typeof value !== 'string' && !Array.isArray(value)) {
        throw new Error(
          `The 'value' argument must be a 'string' or 'array' type, not a '${typeof value}' type.`
        )
      }
      return value
    }

    // Append the value to the existing header. Set the header if it does not exist yet
    append(name, value) {
      this[AppendFn](this[EdgioId], this._validateName(name), this._validateValue(value))
    }

    // Delete the header from the list.
    delete(name) {
      this[RemoveFn](this[EdgioId], this._validateName(name))
    }

    // Return array of header pairs
    entries() {
      const entries = []
      for (const key of this.keys()) {
        entries.push([key, this.get(key)])
      }
      return entries
    }

    // executes callback on each header pair
    forEach(callback) {
      for (const key of this.keys()) {
        callback(this.get(key), key)
      }
    }

    /// Get the value of the specified name. Spec at https://developer.mozilla.org/en-US/docs/Web/API/Headers/get
    /// @param {string} name - the name of the header (throws if parameter is missing)
    /// @returns {(string|null)} - the value of the header. Returns a string of comma separated values if there are multiple values or null if there are no values.
    get(name) {
      // get_header_values returns undefined or an array of string. The spec says
      // it should return a string of comma separated values if there are multiple values.
      const headers = this[GetFn](this[EdgioId], this._validateName(name))
      if (Array.isArray(headers)) return headers.join(', ')

      // Per the spec we return null when there are no values.
      return typeof headers === 'undefined' ? null : headers
    }

    // Returns true of the header name exists, otherwise false
    has(name) {
      return this[CheckFn](this[EdgioId], this._validateName(name))
    }

    // Returns an iterator of header names  - used by runtime.rs to return all response headers to host
    keys() {
      return this[KeysFn](this[EdgioId])
    }

    // Set the header and value. Overwrite any existing headers with the same name
    set(name, value) {
      this[SetFn](this[EdgioId], this._validateName(name), this._validateValue(value))
    }

    // Returns an iterator of header values
    values() {
      const values = []
      for (const key of this.keys()) {
        values.push(this.get(key))
      }
      return values
    }

    // A non-standard method that returns a dictionary of all headers
    // during the JSON generation process.
    toJSON() {
      const json = {}
      for (const key of this.keys()) {
        json[key] = this.get(key)
      }
      return json
    }
  }

  class EdgioResponseHeaders extends EdgioHeaders {
    constructor(responseId, headers) {
      super(responseId, headers, {
        appendFn: host.hostcall_resp_append_header,
        checkFn: host.hostcall_resp_check_header,
        getFn: host.hostcall_resp_get_header_values,
        keysFn: utility.resp_get_header_names,
        removeFn: host.hostcall_resp_remove_header,
        setFn: host.hostcall_resp_set_header,
      })
    }
  }

  class EdgioRequestHeaders extends EdgioHeaders {
    constructor(requestId, headers) {
      super(requestId, headers, {
        appendFn: host.hostcall_req_append_header,
        checkFn: host.hostcall_req_check_header,
        getFn: host.hostcall_req_get_header_values,
        keysFn: utility.req_get_header_names,
        removeFn: host.hostcall_req_remove_header,
        setFn: host.hostcall_req_set_header,
      })
    }
  }

  /// Fetch API Request implementation per https://fetch.spec.whatwg.org/#request-class
  globalThis.Request = class {
    constructor(input, options) {
      this[Options] = options || {}

      // If it already has a requestId, this is fully formed request from the host.
      if (typeof options?.edgio?.id === 'number') {
        this[EdgioId] = options?.edgio?.id
        this[Headers] = new EdgioRequestHeaders(this[EdgioId])
        host.hostcall_debug(
          `sdk.js:Request(${input}, ${JSON.stringify(options)}) id: ${this[EdgioId]}`
        )
        return
      }
      // Otherwise we need to create a new Request in the host and set its properties.
      if (typeof input !== 'string') {
        // TODO: add support for `const req = new Request(request);`
        throw new Error(`"string" type is accepted for the input parameter, not '${typeof input}'`)
      }
      const url = input

      // Method is undefined or string.
      if (this[Options].method !== undefined && typeof this[Options].method !== 'string') {
        throw new Error(
          `options.method must be type 'undefined' or 'string', not '${typeof this[Options]
            .method}'`
        )
      }
      const method = this[Options].method || 'GET'
      this[EdgioId] = host.hostcall_req_create(method, url)
      // We invoke the setting of the headers after we have set the URL. First, we are forced
      // to do this with the API but also we want the `host` header, if such exists, to win
      // over any domain that the user has specified in the URL.
      this[Headers] = new EdgioRequestHeaders(this[EdgioId], this[Options]?.headers || {})

      const body = this[Options].body
      delete this[Options].body // don't keep a copy of the body in case it is large
      this[BodyUsed] = false

      if (body) {
        if (method === 'GET' || method === 'HEAD') {
          throw new Error('Request with GET/HEAD method cannot have body.')
        }

        if (typeof body === 'string') {
          host.hostcall_debug(`new Request(${this[EdgioId]}, string: ${body})`)
          host.hostcall_req_set_body_from_string(this[EdgioId], body)
        } else if (body instanceof ArrayBuffer) {
          // Handle ArrayBuffer directly
          host.hostcall_debug(`new Request(ArrayBuffer: len=${body.byteLength})`)
          host.hostcall_req_set_body_from_array_buffer(this[EdgioId], body)
        } else if (ArrayBuffer.isView(body)) {
          // Handle TypedArrays directly
          host.hostcall_debug(`new Request(TypedArray: len=${body.byteLength})`)
          host.hostcall_req_set_body_from_array_buffer(this[EdgioId], body.buffer)
        } else {
          // TODO: Support for Blobs(), DataView, FormData, ReadableStream, URLSearchParams, String
          // Best effort on other types of bodies
          host.hostcall_debug(`new Request(other: ${body.toString()})`)
          host.hostcall_req_set_body_from_string(this[EdgioId], body.toString())
        }
      }
      host.hostcall_debug(
        `sdk.js:Request(${input}, ${JSON.stringify(options)}) id: ${this[EdgioId]}`
      )
    }

    async arrayBuffer() {
      const stream = new EdgioReadableRequestStream(this[EdgioId])
      const body = await stream.getReader().read()
      if (body.done) {
        return new ArrayBuffer(0)
      }
      this[BodyUsed] = true
      return body.value.buffer
    }

    blob() {
      throw new Error(
        `${InternalErrorPrefix}The 'blob' method is not supported by the Request class.`
      )
    }

    get body() {
      // According to the spec, body is null for GET/HEAD
      if (this.method === 'GET' || this.method === 'HEAD') {
        return null
      }
      return new EdgioReadableRequestStream(this[EdgioId])
    }

    get bodyUsed() {
      // TODO: Should/Can this be moved to the EdgioReadableRequestStream class
      return this[BodyUsed]
    }

    get cache() {
      throw new Error(
        `${InternalErrorPrefix}The 'cache' property is not supported by the Request class.`
      )
    }

    get credentials() {
      throw new Error(
        `${InternalErrorPrefix}The 'credentials' property is not supported by the Request class.`
      )
    }

    clone() {
      throw new Error(
        `${InternalErrorPrefix}The 'clone' method is not supported by the Request class.`
      )
    }

    get destination() {
      throw new Error(
        `${InternalErrorPrefix}The 'destination' property is not supported by the Request class.`
      )
    }

    formData() {
      throw new Error(
        `${InternalErrorPrefix}The 'formData' method is not supported by the Request class.`
      )
    }

    get headers() {
      return this[Headers]
    }

    get integrity() {
      throw new Error(
        `${InternalErrorPrefix}The 'integrity' property is not supported by the Request class.`
      )
    }

    async json() {
      return JSON.parse(await this.text())
    }

    get method() {
      return host.hostcall_req_get_method(this[EdgioId])
    }

    set method(method) {
      return host.hostcall_req_set_method(this[EdgioId], method)
    }

    get mode() {
      throw new Error(
        `${InternalErrorPrefix}The 'mode' property is not supported by the Request class.`
      )
    }

    get redirect() {
      throw new Error(
        `${InternalErrorPrefix}The 'redirect' property is not supported by the Request class.`
      )
    }

    get referrer() {
      throw new Error(
        `${InternalErrorPrefix}The 'referrer' property is not supported by the Request class.`
      )
    }

    get referrerPolicy() {
      throw new Error(
        `${InternalErrorPrefix}The 'referrerPolicy' property is not supported by the Request class.`
      )
    }

    get signal() {
      throw new Error(
        `${InternalErrorPrefix}The 'signal' property is not supported by the Request class.`
      )
    }

    async text() {
      const stream = new EdgioReadableRequestStream(this[EdgioId])
      const body = await stream.getReader().read()
      // Where there is no body, text() returns '' and leaves bodyUsed = false
      if (body.done) {
        return ''
      }

      this[BodyUsed] = true
      return new TextDecoder('utf-8').decode(body.value)
    }

    get url() {
      return host.hostcall_req_get_uri(this[EdgioId])
    }

    set url(uri) {
      return host.hostcall_req_set_uri(this[EdgioId], uri)
    }

    get path() {
      return host.hostcall_req_get_path(this[EdgioId])
    }

    get edgioId() {
      return this[EdgioId]
    }

    toJSON() {
      return {
        method: this.method,
        url: this.url,
        headers: this.headers,
      }
    }

    cloneWithoutBody() {
      const clone_id = host.hostcall_req_clone_without_body(this[EdgioId])
      const req = new Request(null, {
        edgio: {
          id: clone_id,
        },
      })
      return req
    }
  }

  /// Fetch API Response implementation per https://fetch.spec.whatwg.org/#response-class
  /// Where things are not formally defined (e.g. what is the default status code) we follow
  /// Chrome's implementation.
  globalThis.Response = class {
    constructor(body, options) {
      // These variables are concepts that are not supported in Sailfish so we need
      // to keep track of them in this instance's storage.
      this[BodyUsed] = false
      this[BodyDefined] = false
      this[Redirected] = false
      this[Url] = null

      if (typeof options?.edgio?.id === 'number') {
        // It is a response from the host, so we are given the ID.
        this[EdgioId] = options?.edgio?.id
        this[Headers] = new EdgioResponseHeaders(this[EdgioId])
        host.hostcall_debug(`_edgioNew Response: ${this[EdgioId]}`)
        this[BodyDefined] = true // assume the host has a body. TODO: not always correct
        // No need to set all the properties, because the host already has them.
        return
      }

      // It's a new Response rather than an existing from from the host,
      // create a new Response in the host and set its properties.
      this[Options] = options || {}
      // statusMessage is not supported by Sailfish yet.
      // this[Options].statusMessage = this[Options].statusMessage || "OK"

      // Default response is 200 OK
      this[Options].status = this[Options]?.status || 200
      if (this[Options].status < 200 || this[Options].status > 599) {
        throw new Error(
          `The status provided ${this[Options].status} is outside of the standard range [200, 599].`
        )
      }
      this[EdgioId] = host.hostcall_resp_create(this[Options].status)
      this[Headers] = new EdgioResponseHeaders(this[EdgioId], this[Options]?.headers || {})
      host.hostcall_debug(
        `new Response: ${this[EdgioId]} ${JSON.stringify(this[Options]?.headers)}`
      )

      // TODO: Can we convert everything to ArrayBuffer and just have one host function?
      if (body) {
        if (typeof body === 'string') {
          this[BodyDefined] = true
          host.hostcall_debug(`new Response(string: ${body})`)
          host.hostcall_resp_set_body_from_string(this[EdgioId], body)
        } else if (body instanceof ArrayBuffer) {
          // Handle ArrayBuffer directly
          this[BodyDefined] = true
          host.hostcall_debug(`new Response(ArrayBuffer: len=${body.byteLength})`)
          host.hostcall_resp_set_body_from_array_buffer(this[EdgioId], body)
        } else if (ArrayBuffer.isView(body)) {
          // Handle TypedArrays directly
          this[BodyDefined] = true
          host.hostcall_debug(`new Response(TypedArray: len=${body.byteLength})`)
          host.hostcall_resp_set_body_from_array_buffer(this[EdgioId], body.buffer)
        } else {
          // TODO: Support for Blobs(), DataView, FormData, ReadableStream, URLSearchParams, String
          // Best effort on other types of bodies
          this[BodyDefined] = true
          host.hostcall_debug(`new Response(other: ${body.toString()})`)
          host.hostcall_resp_set_body_from_string(this[EdgioId], body.toString())
        }
      }
    }

    async arrayBuffer() {
      if (this[BodyDefined]) {
        this[BodyUsed] = false
        const body = await this.body.getReader().read()
        if (body.done) {
          return new ArrayBuffer(0)
        }
        return body.value.buffer
      }
      return new ArrayBuffer(0)
    }

    blob() {
      throw new Error(
        `${InternalErrorPrefix}The 'blob' method is not supported by the Response class.`
      )
    }

    get body() {
      if (this[BodyDefined]) {
        return new EdgioReadableResponseStream(this[EdgioId])
      }
      // The spec says to return null when no body was given.
      return null
    }

    get bodyUsed() {
      return this[BodyUsed]
    }

    clone() {
      throw new Error(
        `${InternalErrorPrefix}The 'clone' method is not supported by the Response class.`
      )
    }

    formData() {
      throw new Error(
        `${InternalErrorPrefix}The 'formData' method is not supported by the Response class.`
      )
    }

    get headers() {
      return this[Headers]
    }

    async json() {
      return JSON.parse(await this.text())
    }

    get ok() {
      let status = this.status
      return status >= 200 && status < 300
    }

    static redirect(url, status) {
      return new Response(undefined, { headers: { location: url }, status: status || 302 })
    }

    set redirected(newRedirected) {
      this[Redirected] = newRedirected
    }

    get redirected() {
      return this[Redirected]
    }

    get status() {
      return host.hostcall_resp_get_status(this[EdgioId])
    }

    get statusText() {
      return host.hostcall_resp_get_reason(this[EdgioId])
    }

    async text() {
      this[BodyUsed] = true

      if (!this[BodyDefined]) {
        return ''
      }

      const body = await this.body.getReader().read()
      return new TextDecoder('utf-8').decode(body.value)
    }

    get type() {
      throw new Error(
        `${InternalErrorPrefix}The 'type' property is not supported by the Response class.`
      )
    }

    get url() {
      return this[Url]
    }

    get edgioId() {
      return this[EdgioId]
    }

    toJSON() {
      return {
        status: this.status,
        statusText: this.statusText,
        headers: this.headers,
      }
    }

    cloneWithoutBody() {
      const clone_id = host.hostcall_resp_clone_without_body(this[EdgioId])
      const resp = new Response(null, {
        edgio: {
          id: clone_id,
        },
      })
      resp[Url] = this[Url]
      resp[Redirected] = this[Redirected]
      return resp
    }
  }

  // when fetch(/url, { redirect: 'manual' }) encounters a redirect, it returns this object
  // rather than a read Response
  globalThis.OpaqueRedirect = class extends globalThis.Response {
    constructor(edgioId, url) {
      super('', { edgio: { id: edgioId } })
      this[Headers] = {
        append: () => {},
        delete: () => {},
        entries: () => {
          return []
        },
        forEach: () => {},
        get: () => {
          return null
        },
        has: () => {
          return false
        },
        keys: () => {
          return []
        },
        set: () => {},
        values: () => {
          return []
        },
      }
      this[Url] = url
    }
    get ok() {
      return false
    }

    get status() {
      return 0
    }
  }

  class ResponseError extends Error {
    // Creates a new ResponseError object from an Edgio host error response code.
    constructor(responseErrorCode) {
      super()
      this[ErrorCode] = responseErrorCode
    }

    get code() {
      return this[ErrorCode]
    }

    get message() {
      return host.hostcall_resp_get_error_code_message(this[ErrorCode])
    }

    toString() {
      return `${this.message} (code: ${this.code})`
    }
  }

  // EdgioDict is a proxy class to the Edgio host API.
  class EdgioDict {
    // Creates a new EdgioDict object from a given dict namespace
    constructor(namespace) {
      this[Namespace] = namespace
    }

    /// Get the value of the specified dictionary entry by key
    /// @param {string} key - the key of the dictionary entry
    /// @returns {string} value - the value of the dictionary entry.
    get(key) {
      return host.hostcall_dict_get(this[Namespace], key)
    }

    /// Get the value of the specified dictionary entry by key (base64-decoded)
    /// @param {string} key - the key of the dictionary entry
    /// @returns {string} value - the value of the dictionary entry.
    getBase64Decoded(key) {
      return new Uint8Array(host.hostcall_dict_get_base64_decoded(this[Namespace], key))
    }

    // Returns true if the dictionary key exists, otherwise false
    has(key) {
      return host.hostcall_dict_check(this[Namespace], key)
    }

    // Returns array of dictionary keys
    keys() {
      return utility.dict_get_keys(this[Namespace])
    }

    toJSON() {
      const json = {}
      for (const key of this.keys()) {
        json[key] = this.get(key)
      }
      return json
    }
  }

  // EdgioSet is a proxy class to the Edgio host API.
  class EdgioSet {
    // Creates a new EdgioSet object from a given Set namespace
    constructor(namespace) {
      this[Namespace] = namespace
    }

    // Returns true if the Set key exists, otherwise false
    has(key) {
      return host.hostcall_unique_set_check(this[Namespace], key)
    }

    // Returns array of Set keys
    keys() {
      return utility.unique_set_get_keys(this[Namespace])
    }

    toJSON() {
      return this.keys()
    }
  }

  // EdgioMetrics is a proxy class to the Edgio host API.
  class EdgioMetrics {
    /// Start a timer with the index of the customer metric
    /// @param {number} index - the index of the timer
    /// @returns {number} timerId - the id of the timer to use to stop the timer
    startTimer(index) {
      return host.hostcall_metrics_start_timer(index)
    }
    /// Stop a timer with the index of the customer metric
    /// @param {number} timerId - the id of the timer to use to stop the timer
    stopTimer(timerId) {
      return host.hostcall_metrics_stop_timer(timerId)
    }
    /// Add a value to the customer metric
    /// @param {number} index - the index of the metric
    /// @param {number} value - the value to add
    add(index, value) {
      return host.hostcall_metrics_add(index, value)
    }
  }

  globalThis.edgio = {
    env: new EdgioDict(ENV_VAR_NAMESPACE_EDGIO_CONFIG),
    origins: new EdgioSet(ENV_VAR_NAMESPACE_EDGIO_ORIGINS),
    usrvar: new EdgioDict(ENV_VAR_NAMESPACE_EDGIO_USRVAR),
    metrics: new EdgioMetrics(),
  }

  // We need to inject a "module" object into the global scope so that the bundled
  // code produced by the build system can be executed. The module object can be empty.
  globalThis.module = {}

  // __dirname and __filename are shimmed as a lot of code expects them to be available.
  globalThis.__dirname = '/'
  globalThis.__filename = '/index.js'

  // shims for the global object
  globalThis.global = globalThis

  // Inject the TextEncoder/TextDecoder polyfill into the global object.
  // Be sure to include the version that has encodeInto() support.
  // Inject the TextEncoder/TextDecoder polyfill into the global object.
  const { TextEncoder, TextDecoder } = require('fastestsmallesttextencoderdecoder-encodeinto')
  globalThis.TextDecoder = TextDecoder
  globalThis.TextEncoder = TextEncoder

  // Add the missing getting for TextEncoder.encoding() which always returns 'utf-8'
  Object.defineProperties(globalThis.TextEncoder.prototype, {
    encoding: {
      get: function () {
        return 'utf8'
      },
    },
  })

  // An array containing a list of "fetch" callbacks added via
  // `addEventListener` in the user-function. These are executed
  // one by one, blocking the progress on every one that returns a
  // promise and stopping the iteration on the first one that
  // invokes `event.respondWith`.
  let fetch_listeners = []

  // Used to indicate that the main loop is currently waiting on a promise
  // from the last invoked listener. We hold off the execution of the next
  // listener until the promise is resolved.
  let waiting_on_listener_promise = false

  /// Value of a rejection that was not otherwise handled and was caught
  /// by the main execution loop.
  let unhandled_rejection = null

  /// A map of pending fetches. The key is the request ID. Every time a new
  /// fetch is issued, it is added to this map. When the fetch is resolved,
  /// the corresponding entry is removed from the map.
  let pending_fetches = new Map()

  // Used to indicate that the user-function has fully created a synthetic
  // response and that this WASM function should return.
  let respond_with_finished = false

  // Counts the number of times that waitUntil has been used.
  let wait_until_used_counter = 0

  // Counts the number of times that waitUntil has finished processing.
  let wait_until_finished_counter = 0

  globalThis.addEventListener = function (type, listener, _options) {
    // TODO: Should this throw if not 2 arguments?
    if (type === 'fetch') {
      fetch_listeners.push(listener)
    } else {
      console.warn(`Unsupported event type: ${type}`)
    }
  }

  function expect_number_of_args(args, expected) {
    if (args.length !== expected) {
      throw new Error(`Expected ${expected} arguments, got ${args.length}`)
    }
  }

  function expect_min_number_of_args(args, expected) {
    if (args.length < expected) {
      throw new Error(`Expected at least ${expected} arguments, got ${args.length}`)
    }
  }

  function create_event_object(req) {
    return {
      type: 'fetch',
      request: req,
      respondWith: function (responsePromise) {
        expect_number_of_args(arguments, 1)

        if (responsePromise instanceof Response) {
          let response = responsePromise
          host.hostcall_debug(
            'sdk.js respondWith: got response object, handle synchronously and return'
          )
          host.hostcall_resp_send_to_client(response.edgioId)
          respond_with_finished = true
          return undefined
        }

        // Use Promise.resolve to promisify a non-promise value.
        // If the value is a promise then this is a noop.
        host.hostcall_debug('sdk.js respondWith: resolving response promise')
        Promise.resolve(responsePromise)
          .then(response => {
            host.hostcall_resp_send_to_client(response.edgioId)
            respond_with_finished = true
            return response
          })
          .catch(err => {
            respond_with_finished = false
            throw new Error('Failed to resolve respondWith promise: ' + err)
          })
      },
      waitUntil: function (promise) {
        expect_number_of_args(arguments, 1)

        ++wait_until_used_counter
        // waitUntil ignores the return value of the promise.
        // All we care about is that we wait out the promise.
        Promise.resolve(promise)
          .then(response => {
            ++wait_until_finished_counter
            return response
          })
          .catch(err => {
            ++wait_until_finished_counter
            throw new Error('Failed to resolve waitUntil promise: ' + err)
          })
      },
    }
  }

  function parseCookie(cookie) {
    const parts = cookie.split('=')
    // Use trim because sometimes ';' is used and sometimes '; ' is used as cookie delimiter.
    const key = parts[0].trim()
    const value = parts[1]
    return { key, value }
  }

  // Inject response.headers.set-cookie values into the options.headers.cookie
  // Returns an array or set-cookie values. (Array is empty if no set-cookies found.)
  function processSetCookies(options, response) {
    // Make a dictionary for cookie values so we can deduplicate cookie names
    let deduplicatedCookies = {}

    // Start with any existing cookie values and populate cookieValues
    const existingCookies = options.headers.cookie
    if (existingCookies) {
      const individualCookies = existingCookies.split(';')
      for (let i = 0; i < individualCookies.length; i++) {
        const { key, value } = parseCookie(individualCookies[i])
        deduplicatedCookies[key] = value
      }
    }

    // get setCookies and ensure it is an array so we can iterate on it.
    let setCookies = response.headers.get('set-cookie') || []
    if (!Array.isArray(setCookies)) {
      setCookies = [setCookies]
    }

    for (let i = 0; i < setCookies.length; i++) {
      // TODO: Do we need to honor SameSite=, Path= and Http(s)Only?
      const { key, value } = parseCookie(setCookies[i].split(';')[0])
      deduplicatedCookies[key] = value
    }

    // Assemble the new cookie: header if there are any cookies
    const keys = Object.keys(deduplicatedCookies)
    if (keys.length > 0) {
      options.headers.cookie = ''
      for (let i = 0; i < keys.length; i++) {
        const key = keys[i]
        const value = deduplicatedCookies[key]
        options.headers.cookie += `${key}=${value}${i < keys.length - 1 ? '; ' : ''}`
      }
    }
  }

  // redirect =
  //   - "follow" - Make a new fetch to follow any redirect responses (cookies/set-cookies are honored on redirect)
  //   - "manual" - Return "opaqueredirect" type on any redirect responses, does not follow direct
  //       { body:null,bodyUsed:false,headers:{},ok:false,redirected:false,status:0,statusText:"",type:"opaqueredirect",url:<initial URL>}
  //   - "error" - Throws exception on any redirect responses
  // Return an error when the total # of redirects exceeds MAX_REDIRECTS
  async function handleRedirect(originalUrl, options, redirectedCount, response) {
    const MAX_REDIRECTS = 5 // so this is 1 fetch + 5 redirects for a total of 6 fetches

    // If the response is not a redirect, or there is no location header, resolve the promise normally
    const location = response.headers.get('location')
    if ([301, 302, 303, 307, 308].indexOf(response.status) < 0 || !location) {
      response[Url] = originalUrl
      response.redirected = redirectedCount > 0
      return response
    }

    const redirect = options.redirect
    if (redirect === 'error') {
      throw new Error('redirect')
    }

    if (redirect === 'manual') {
      // When we receive a redirect status code, and the redirect method is 'manual'
      // the specs say to return this OpaqueRedirect object.
      return new globalThis.OpaqueRedirect(response.edgioId, originalUrl)
    }

    // Check for MAX_REDIRECTS before following the next redirect.
    if (++redirectedCount > MAX_REDIRECTS) {
      throw new Error(`fetch(${originalUrl}) exceeded the limit of ${MAX_REDIRECTS} redirects.`)
    }

    // Redirects forward all the original request headers to the redirect request headers via 'options'
    // Redirects aggregate the original response set-cookies and sends them as 'cookie' to the redirect request headers.
    processSetCookies(options, response)

    // Issue a new request that follows the redirect
    const newResponse = await issueFetch(location, options, redirectedCount)
    return newResponse
  }

  async function issueFetch(urlOrRequest, options, redirectedCount) {
    let req
    if (urlOrRequest instanceof Request) {
      req = urlOrRequest
    } else {
      // globalThis.fetch() and handleRedirect() ensure options always exists.
      req = new Request(urlOrRequest, options)
    }

    // Set the provided fetch origin
    try {
      host.hostcall_req_set_fetch_origin(req.edgioId, options.edgio.origin)
    } catch {
      throw new Error(
        `Invalid edgio.origin specified. Must be one of: ${edgio.origins.keys().join(',')}`
      )
    }

    // Start the async fetch
    host.hostcall_req_fetch_async(req.edgioId)

    // Create a new promise and capture its resolve and reject
    // functions so that we can invoke them from the outside.
    let resolve
    let reject
    const promise = new Promise((promise_resolve, promise_reject) => {
      resolve = promise_resolve
      reject = promise_reject
    })

    // Add the promise to the list of pending promises
    pending_fetches.set(req.edgioId, {
      promise,
      resolve,
      reject,
    })

    // After this await, the original `Request` object is no longer valid in the host,
    // and must not be used again. Because of this, we have to copy any fields we need available
    // after this call.
    const request_url = req.url
    const response = await promise

    return handleRedirect(request_url, options, redirectedCount, response)
  }

  globalThis.fetch = async function (urlOrRequest, options) {
    expect_min_number_of_args(arguments, 1)

    // Validate the options. Ensure .redirect and .headers exist
    options = options || {}
    if (!options.redirect) {
      options.redirect = 'follow'
    }
    if (!options.headers) {
      options.headers = {}
    }
    if (!options.edgio) {
      options.edgio = {}
    }
    const validRedirects = ['error', 'follow', 'manual']
    if (validRedirects.indexOf(options.redirect) < 0) {
      throw new Error(
        `redirect must be one of "${validRedirects.join(', ')}", not "${options.redirect}"`
      )
    }

    // All fetches must specify an explicit origin (for now).
    if (!options.edgio.origin) {
      throw new Error(
        `edgio.origin must be specified as a fetch option. Must be one of: ${edgio.origins
          .keys()
          .join(',')}`
      )
    }

    return issueFetch(urlOrRequest, options, 0)
  }

  // Service worker API entry point.
  function main(edgioRequestId) {
    const path = edgio.env.get('__EDGE_FUNCTION_PATH__')
    // Get the user function from the edge functions map as generated during the bundling.
    const edgeFunction = edgeFunctions.get(path)
    if (!edgeFunction) {
      throw new Error(`No edge function found for path: ${path}`)
    }

    // Run the user function that expects service worker API to be available.
    // Bind globalThis to the function so the third-party frameworks work properly.
    edgeFunction().bind(globalThis)()
    if (fetch_listeners.length === 0) {
      host.hostcall_debug('No fetch listeners, returning immediately')
      return
    }
    host.hostcall_debug(`Got ${fetch_listeners.length} fetch listeners`)

    const req = new Request(null, {
      edgio: {
        id: edgioRequestId,
      },
    })
    const event_object = create_event_object(req)

    // There are no more pending jobs if and only if:
    // * respondWith has finished
    // * There are no more pending waitUntil promises
    // This means that even if there are pending fetches, we should stop running.
    function jobs_are_not_pending() {
      return respond_with_finished && wait_until_finished_counter >= wait_until_used_counter
    }

    // This is the main loop that is executing the event listeners, pending jobs and pending promises.
    // It:
    // * Executes the next event listener if there is one and we are not waiting on a promise from the previous one.
    // * Executes pending synchronous QuickJS jobs if there are any.
    // * Evaluates if there are unhanded rejections and if so, throws them.
    // * Waits on pending fetches if there are any.
    /*eslint no-constant-condition: off*/
    while (true) {
      while (!waiting_on_listener_promise && fetch_listeners.length > 0) {
        const listener = fetch_listeners.shift()
        const promise = listener(event_object)
        if (promise instanceof Promise) {
          waiting_on_listener_promise = true
          promise
            .then(() => {
              // The result of a fetch listener is ignored by the service worker API.
              waiting_on_listener_promise = false
            })
            .catch(err => {
              // TODO: Per https://jira.atlas.llnw.com/browse/EAPPS-208
              // we must handle the rejected promises by using the provided
              // QuickJS API, not in our own JavaScript code which might have
              // errors of its own. To emulate the silent rejection, throw
              // an exception in `globalThis.fetch` and then throw again
              // in this method.
              waiting_on_listener_promise = false
              unhandled_rejection = err
            })
        }
        if (jobs_are_not_pending()) {
          host.hostcall_debug('breaking the loop - no more jobs (1)')
          return
        }
      }

      host.hostcall_debug('Executing pending jobs...')

      // Executes all the currently pending jobs inside of the QuickJS runtime.
      // This may setup new pending fetches which is why we execute it before checking
      // if there are any more pending fetches left. If an exception is thrown, this
      // will short-circuit and return the error.
      utility.execute_pending()

      // If there is an unhandled rejection, throw it in the main loop so that
      // it propagates to the host.
      if (unhandled_rejection) {
        const err = unhandled_rejection
        unhandled_rejection = null
        throw err
      }

      if (pending_fetches.size === 0) {
        if (fetch_listeners.length === 0 && !waiting_on_listener_promise) {
          return
        }
      } else {
        // Wait for the next ready response. This call blocks.
        const [is_response, req_id, resp_id_or_err_code] = host.hostcall_resp_await_next()

        host.hostcall_debug(
          `sdk.js->hostcall_resp_await_next: is_response(${is_response}), req_id(${req_id}), resp_id_or_err_code(${resp_id_or_err_code})`
        )

        // Get the pending request that has a ready response.
        const pending_fetch = pending_fetches.get(req_id)
        if (!pending_fetch) {
          throw new Error(`No pending fetch for ID ${req_id}`)
        }
        pending_fetches.delete(req_id)

        // Resolve or reject the promise of the pending request based on
        // the is_response flag.
        const { resolve, reject } = pending_fetch
        if (is_response) {
          host.hostcall_debug('sdk.js: creating Response')
          const resp = new Response(null, {
            edgio: {
              id: resp_id_or_err_code,
            },
          })
          resolve(resp)
        } else {
          host.hostcall_debug('sdk.js: creating ResponseError')
          const err = new ResponseError(resp_id_or_err_code)
          reject(err)
        }
      }

      if (jobs_are_not_pending()) {
        host.hostcall_debug('breaking the loop - no more jobs (2)')
        return
      }
    }
  }

  main(edgioRequestId)
}
